*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
}

.intro {
  background-color: rgb(241, 226, 215);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  width: 100%;
  overflow: hidden;
  flex-direction: row;
}

.img {
  height: 230px;
  width: 230px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center top;
  transform: scale(1.2);
  max-width: 100%;
}

.contentContainer {
  width: 40%;
  margin: 0px 50px;
  max-width: 100%;
}

.icons {
  display: flex;
  gap: 20px;
  font-size: 30px;
}

.download-link {
  text-decoration: none;
}

.download-btn {
  display: inline-block;
  padding: 4px 6px;
  font-size: 14px;
  height: 30px;
  position: relative;
  top: 3px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  background-color: rgb(71, 71, 71);
  color: white;
  border-radius: 5px;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.download-btn:hover {
  background-color: grey;
  color: black;
}

.download-btn:active {
  transform: scale(0.98);
}

@media screen and (max-width: 768px) {
  .contentContainer {
    width: 60%;
    margin: 0px 20px;
  }

  .img {
    height: 180px;
    width: 180px;
    margin: 10px 0px;
  }

  .icons {
    font-size: 25px;
  }

  .intro {
    padding: 10px;
    flex-direction: column;
  }
}

@media screen and (max-width: 480px) {
  .contentContainer {
    width: 80%;
    margin: 0px;
  }

  .img {
    height: 150px;
    width: 150px;
    margin: 10px 0px;
    transform: scale(1);
  }

  .icons {
    display: flex;
    justify-content: flex-start;
    font-size: 20px;
    gap: 10px;
  }

  .download-btn {
    position: relative;
    top: -4px;
    padding: 2px 8px;
  }

  .intro {
    padding: 10px;
    height: auto;
    flex-direction: column;
  }
}
